html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

ul,
ol {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
  word-break: break-word;
}

ul[class] {
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

a {
  text-decoration: none;
}

@font-face {
  font-weight: 400;
  font-family: "Gilroy";
  font-style: normal;
  src: url("../fonts/Gilroy-Regular.woff2") format("woff2"), url("../fonts/Gilroy-Regular.woff") format("woff");
}
@font-face {
  font-weight: 500;
  font-family: "Gilroy";
  font-style: normal;
  src: url("../fonts/Gilroy-Medium.woff2") format("woff2"), url("../fonts/Gilroy-Medium.woff") format("woff");
}
@font-face {
  font-weight: 700;
  font-family: "Gilroy";
  font-style: normal;
  src: url("../fonts/Gilroy-Bold.woff2") format("woff2"), url("../fonts/Gilroy-Bold.woff") format("woff");
}
.main-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  font-weight: 700;
  line-height: 20px;
  color: #ffffff;
  background-color: #685ed6;
  padding: 10px 20px;
  border-radius: 6px;
}
.main-btn:hover {
  background-color: #33afe1;
}
.main-btn:active {
  background-color: #18e1f7;
}

.main-btn--pink {
  background-color: #f1e4ff;
  color: #685ed6;
}
.main-btn--pink:hover {
  background-color: #c39bfb;
}
.main-btn--pink:active {
  background-color: #685ed6;
  color: #ffffff;
}

html,
body {
  height: 100%;
  min-width: 378px;
  font-family: "Gilroy", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  font-style: normal;
  line-height: 26px;
  color: #1d2530;
  padding: 0;
  margin: 0;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.page-main {
  flex: 1 0 auto;
}

.page-footer {
  flex: 0 0 auto;
}

.container {
  width: 378px;
  margin: 0 auto;
  padding: 0 23px;
}

@media (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 40px;
  }
}
@media (min-width: 1210px) {
  .container {
    width: 1210px;
    padding: 0 50px;
  }
}
.visually-hidden:not(:focus):not(:active),
input[type=checkbox].visually-hidden,
input[type=radio].visually-hidden {
  position: absolute;
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.main-nav {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  padding: 30px 30px 115px;
}

.main-nav__login,
.main-nav__registration {
  width: 177px;
  height: 40px;
  margin-bottom: 10px;
}

@media (min-width: 768px) {
